import styled from 'styled-components';

const Section = styled.div`
    min-height: 400px
    min-width: 280px
    max-height: 450px
    max-width: 300px
    flex-grow: 1;
    display: flex;
`;

const TopSection = styled.div`
    min-height: 400px
    max-height: 450px
    width: 100%
`;

export {Section, TopSection};


